@input-box-height: 35px;

.@{prefix-calendar}-range {
  width: 502px;
  overflow: hidden;

  &-part {
    width: 250px;
    position: relative;

    .@{prefix-calendar}-time-picker {
      top: 69px;
      &-panel {
        &-select {
          width: 77px;
        }
      }
    }
  }

  &-left {
    float: left;
    .@{prefix-calendar}-time-picker-panel {
      &-select:last-child {
        border-right: 1px solid #e9e9e9;
      }

    }
  }

  &-right {
    float: right;
    .@{prefix-calendar}-time-picker-panel {
      left: 21px;

      &-select:first-child {
        border-left: 1px solid #e9e9e9;
      }
    }
  }

  &-middle {
    position: absolute;
    margin-left: -10px;
    text-align: center;
    height: @input-box-height;
    line-height: @input-box-height;
  }
  .@{prefix-calendar}-date-panel::after {
    content:".";
    display:block;
    height:0;
    clear:both;
    visibility:hidden;
  }

  .@{prefix-calendar}-input-wrap {
    height: @input-box-height;
  }
  .@{prefix-calendar}-input,
  .@{timePickerClass}-input {
    padding: 1px 7px;
    height: 22px;
  }

  .@{prefix-calendar}-body,
  .@{prefix-calendar}-decade-panel-body,
  .@{prefix-calendar}-year-panel-body,
  .@{prefix-calendar}-month-panel-body {
    border-bottom: 1px solid #e9e9e9;
  }

  &.@{prefix-calendar}-week-number {
    width: 574px;

    .@{prefix-calendar}-range {
      &-part {
        width: 286px;
        .@{prefix-calendar}-time-picker {
          top: 69px;
          &-panel {
            &-select {
              width: 89px;
            }
          }
        }
      }
      &-right {
        .@{prefix-calendar}-time-picker-panel {
          left: 36px;
        }
      }
    }
  }

  .@{prefix-calendar}-year-panel,
  .@{prefix-calendar}-month-panel,
  .@{prefix-calendar}-decade-panel {
    top: @input-box-height;
  }
  .@{prefix-calendar}-month-panel .@{prefix-calendar}-year-panel {
    top: 0;
  }
  .@{prefix-calendar}-decade-panel-table,
  .@{prefix-calendar}-year-panel-table,
  .@{prefix-calendar}-month-panel-table {
    height: 198px;
  }

  .@{prefix-calendar}-in-range-cell {
    background: #ebf4f8;
    border-radius: 0;
  }

  &-bottom {
    text-align: right;
  }

  .@{prefix-calendar}-footer{
    border-top: none;
    padding: 0;
    &-btn {
      padding: 10px 12px 10px 0;
    }
  }
  .@{prefix-calendar}-ok-btn {
    position: static;
  }
  .@{prefix-calendar}-today-btn {
    float: left;
  }
}



