@prefix-select: rc-select;

.@{prefix-select} {
  box-sizing: border-box;
  display: inline-block;
  margin: 0;
  padding: 0px;
  position: relative;
  vertical-align: middle;
  color: #666;
  font-size: @font-size-base;

  > ul > li > a {
    padding: 0;
    list-style: none;
    background-color: #fff;
  }

  * {
    outline: none;
  }

  // arrow
  &-arrow {
    position: absolute;
    top: 50%;
    right: 6px;
    line-height: 1;
    height: 24px;
    margin-top: -3px;

    & b {
      border-color: #999999 transparent transparent transparent;
      border-style: solid;
      border-width: 5px 4px 0 4px;
      height: 0;
      width: 0;
      margin-left: -7px;
      margin-top: -11px;
      position: absolute;
      top: 50%;
      left: 50%;
      transition: transform 0.2s ease;
    }
  }

  // selection
  &-selection {
    outline: none;
    user-select: none;

    box-sizing: border-box;
    display: block;

    background-color: #fff;
    border-radius: @border-radius-base;
    border: 1px solid @border-color-base;
    transition: all .3s @ease-in-out;
  }

  &-disabled {
    color: #ccc;
  }

  &-disabled &-selection {
    background: #f4f4f4;
    cursor: not-allowed;
    &:hover,
    &:active {
      border-color: @border-color-base;
    }
  }

  &-open &-arrow b {
    border-color: transparent transparent #888 transparent;
    border-width: 0 4px 5px 4px;
  }

  &-disabled &-selection--multiple &-selection__choice {
    background: #e9e9e9;
    color: #aaa;
    padding-right: 10px;
    &__remove {
      display: none;
    }
  }

  &-selection--single {
    height: 24px;
    position: relative;
    cursor: pointer;

    .@{prefix-select}-selection__rendered {
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      padding: 0 12px;
      line-height: 24px;
      position: relative;
    }

    .@{prefix-select}-selection__placeholder {
      color: #ccc;
    }
  }

  &-lg {
    .rc-select-selection--single {
      height: 36px;
      .rc-select-selection__rendered {
        line-height: 36px;
      }
    }

    .rc-select-selection--multiple {
      min-height: 36px;
      .rc-select-selection__rendered {
        li {
          height: 24px;
          line-height: 24px;
        }
      }
    }
  }

  &-sm {
    * {
      outline: none;
    }
    .rc-select-selection--single {
      height: 24px;
      .rc-select-selection__rendered {
        line-height: 24px;
      }
    }
    .rc-select-selection--multiple {
      min-height: 24px;
      .rc-select-selection__rendered {
        li {
          height: 14px;
          line-height: 14px;
        }
      }
    }
  }

  &-disabled &-selection__choice__remove {
    color: #ccc;
    cursor: default;
    &:hover {
      color: #ccc;
    }
  }

  &-search__field__wrap {
    display: inline-block;
    position: relative;
    input {
      border: none;
    }
  }

  &-search__field__placeholder {
    position: absolute;
    top: 50%;
    left: 9px;
    color: #ccc;
    line-height: 20px;
    height: 20px;
    margin-top: -10px;
    cursor: text;
  }

  // open
  &-open {
    .@{prefix-select}-selection {
      border-color: tint(@primary-color, 20%);
      outline: 0;
      box-shadow: 0 0 0 2px tint(@primary-color, 80%);
    }
  }

  &-selection--multiple {
    min-height: 28px;
    cursor: text;

    .@{prefix-select}-search--inline {
      width: auto;
      .@{prefix-select}-search__field {
        width: 0.75em;
      }
    }

    .@{prefix-select}-selection__rendered {
      overflow: hidden;
      text-overflow: ellipsis;
      padding-left: 6px;
      padding-bottom: 4px;
      min-height: 24px;
      padding-top: 4px;
      ul {
        padding: 0;
        margin: 0;
        li {
          list-style: none;
          margin-right: 3px;
          margin-bottom: 3px;
          height: 24px;
          line-height: 24px;
        }
      }
    }

    > ul > li {
      margin-top: 4px;
      height: 20px;
      line-height: 20px;
    }

    .@{prefix-select}-selection__choice {
      background-color: #f3f3f3;
      border-radius: 4px;
      cursor: default;
      float: left;
      padding: 0 15px;
      margin-right: 4px;
      max-width: 99%;
      position: relative;
      overflow: hidden;
      transition: padding .3s @ease-in-out;
      padding: 0 20px 0 10px;
    }

    .@{prefix-select}-selection__choice__content {
      display: inline-block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      max-width: 100%;
      transition: margin .3s @ease-in-out;
    }

    .@{prefix-select}-selection__choice__remove {
      color: #999;
      line-height: inherit;
      cursor: pointer;
      display: inline-block;
      font-weight: bold;
      transition: all 0.3s @ease-in-out;
      position: absolute;
      right: 4px;
      padding: 0 0 0 8px;
      &:hover {
        color: #404040;
      }
      &:before {
        content: "x";
      }
    }
  }
}

.@{prefix-select}-dropdown {
  &.slide-up-enter.slide-up-enter-active&-placement-bottomLeft,
  &.slide-up-appear.slide-up-appear-active&-placement-bottomLeft {
    animation-name: rcSlideUpIn;
  }

  &.slide-up-enter.slide-up-enter-active&-placement-topLeft,
  &.slide-up-appear.slide-up-appear-active&-placement-topLeft {
    animation-name: rcSlideDownIn;
  }

  &.slide-up-leave.slide-up-leave-active&-placement-bottomLeft {
    animation-name: rcSlideUpOut;
  }

  &.slide-up-leave.slide-up-leave-active&-placement-topLeft {
    animation-name: rcSlideDownOut;
  }

  &-hidden {
    display: none;
  }

  background-color: white;
  border: 1px solid @border-color-base;
  box-shadow: @box-shadow-base;
  border-radius: @border-radius-base;
  box-sizing: border-box;
  z-index: 1050;
  left: -9999px;
  top: -9999px;
  position: absolute;
  outline: none;
  overflow: hidden;
  font-size: @font-size-base;

  &-menu {
    outline: none;
    margin-bottom: 0;
    padding-left: 0; // Override default ul/ol
    list-style: none;
    max-height: 250px;
    overflow: auto;
    margin-top: 0;

    &-item-group-list {
      margin: 0;
      padding: 0;

      > .@{prefix-select}-dropdown-menu-item {
        padding-left: 24px;
      }
    }

    &-item-group-title {
      color: #999;
      line-height: 1.5;
      padding: 8px 15px;
    }

    &-item {
      position: relative;
      display: block;
      padding: 7px 15px;
      font-weight: normal;
      color: #666;
      white-space: nowrap;
      cursor: pointer;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      transition: background 0.3s ease;

      &:hover,
      &-active {
        background-color: tint(@primary-color, 90%);
      }

      &-selected {
        background-color: tint(@primary-color, 80%);
        &:hover {
          background-color: tint(@primary-color, 80%);
        }
      }

      &-disabled {
        color: #ccc;
        cursor: not-allowed;

        &:hover {
          color: #ccc;
          background-color: #fff;
          cursor: not-allowed;
        }
      }

      &-divider {
        height: 1px;
        margin: 1px 0;
        overflow: hidden;
        background-color: #e5e5e5;
        line-height: 0;
        }
      }
    }
    &-container-open,
    &-open {
      .@{prefix-select}-dropdown {
        display: block;
        background-color: #fff;
        box-shadow: 0 1px 6px rgba(0,0,0,.2);
        border-radius: 4px;
        box-sizing: border-box;
        z-index: 1050;
        left: -9999px;
        top: -9999px;
        position: absolute;
        outline: none;
        overflow: hidden;
        font-size: 12px;
      }
    }

    .@{prefix-select}-dropdown-search {
      display: block;
      padding: 4px;
      .@{prefix-select}-search__field__wrap {
        width: 100%;
      }
      .@{prefix-select}-search__field {
        padding: 4px 7px;
        width: 100%;
        box-sizing: border-box;
        border: 1px solid @border-color-base;
        border-radius: 4px;
        outline: none;
      }
      &.@{prefix-select}-search--hide {
        display: none;
      }
    }
}

@keyframes rcSlideUpIn {
  0% {
    opacity: 0;
    transform-origin: 0% 0%;
    transform: scaleY(.8);
  }
  100% {
    opacity: 1;
    transform-origin: 0% 0%;
    transform: scaleY(1);
  }
}

@keyframes rcSlideUpOut {
  0% {
    opacity: 1;
    transform-origin: 0% 0%;
    transform: scaleY(1);
  }
  100% {
    opacity: 0;
    transform-origin: 0% 0%;
    transform: scaleY(.8);
  }
}

@keyframes rcSlideDownIn {
  0% {
    opacity: 0;
    transform-origin: 100% 100%;
    transform: scaleY(.8);
  }
  100% {
    opacity: 1;
    transform-origin: 100% 100%;
    transform: scaleY(1);
  }
}

@keyframes rcSlideDownOut {
  0% {
    opacity: 1;
    transform-origin: 100% 100%;
    transform: scaleY(1);
  }
  100% {
    opacity: 0;
    transform-origin: 100% 100%;
    transform: scaleY(.8);
  }
}

.rc-search-input .@{prefix-select} {
  width: 100%;

  & + .rc-input-group-wrap > button {
    top: -5px;
  }
}



