.rc-radio-checked:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid #16c4c6;
  content: '';
  animation: antRadioEffect 0.36s ease-in-out;
  animation-fill-mode: both;
  visibility: hidden;
}
.rc-radio:hover:after,
.rc-radio-wrapper:hover .rc-radio:after {
  visibility: visible;
}
.rc-radio-inner {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  width: 12px;
  height: 12px;
  border-width: 1px;
  border-style: solid;
  border-radius: 100px;
  border-color: #16c4c6;
  background-color: #fff;
  transition: all 0.3s;
}
.rc-radio-inner:after {
  position: absolute;
  width: 6px;
  height: 6px;
  left: 3px;
  top: 3px;
  border-radius: 50%;
  display: table;
  border-top: 0;
  border-left: 0;
  content: ' ';
  background-color: #16c4c6;
  opacity: 0;
  transform: scale(0);
  transition: all 0.3s cubic-bezier(0.78, 0.14, 0.15, 0.86);
}
.rc-radio-input {
