@radio-prefix-cls: ~"rc-radio";
@radio-group-prefix-cls: ~"rc-radio-group";
@radio-inner-prefix-cls: ~"rc-radio-inner";
@radio-duration: .3s;

.@{radio-group-prefix-cls} {
  display: inline-block;
  line-height: unset;
}

// 一般状态
.@{radio-prefix-cls}-wrapper {
  display: inline-block;
  position: relative;
  white-space: nowrap;
  margin-right: 8px;
  cursor: pointer;
}

.@{radio-prefix-cls} {
  white-space: nowrap;
  outline: none;
  display: inline-block;
  position: relative;
  line-height: 1;
  vertical-align: text-bottom;
  cursor: pointer;
  .@{radio-prefix-cls}-wrapper:hover &,
  &:hover,
  &-focused {
    .@{radio-inner-prefix-cls} {
      border-color: #16c4c6;
    }
  }
  &-checked:after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 1px solid #16c4c6;
    content: '';
    animation: antRadioEffect 0.36s ease-in-out;
    animation-fill-mode: both;
    visibility: hidden;
  }
  &:hover:after,
  .@{radio-prefix-cls}-wrapper:hover &:after {
    visibility: visible;
  }
  &-inner {
    &:after {
      position: absolute;
      width: 6px;
      height: 6px;
      left: 3px;
      top: 3px;
      border-radius: 50%;
      display: table;
      border-top: 0;
      border-left: 0;
      content: ' ';
      background-color: #16c4c6;
      opacity: 0;
      transform: scale(0);
      transition: all @radio-duration @ease-in-out-circ;
    }

    position: relative;
    top: 0;
    left: 0;
    display: block;
    width: 12px;
    height: 12px;
    border-width: 1px;
    border-style: solid;
    border-radius: 100px;
    border-color: #16c4c6;
    background-color: #fff;
    transition: all @radio-duration;
  }

  &-input {
    position: absolute;
    left: 0;
    z-index: 1;
    cursor: pointer;
    opacity: 0;
    top: 0;
    bottom: 0;
    right: 0;
  }
}

// 选中状态
.@{radio-prefix-cls}-checked {
  .@{radio-inner-prefix-cls} {
    border-color: #16c4c6;
    &:after {
      transform: scale(.875);
      opacity: 1;
      transition: all @radio-duration @ease-in-out-circ;
    }
  }
}

.@{radio-prefix-cls}-disabled {
  .@{radio-inner-prefix-cls} {
    border-color:#16c4c6 !important;
    background-color: #fbfbfb;
    &:after {
      background-color: #fbfbfb;
    }
  }

  .@{radio-prefix-cls}-input {
    cursor: not-allowed;
  }

  & + span {
    color: #b5b5b5;
    cursor: not-allowed;
  }
}

span.@{radio-prefix-cls} + * {
  padding-left: 8px;
  padding-right: 8px;
}


@keyframes antRadioEffect {
  0% {
    transform: scale(1);
    opacity: 0.5;
  }
  100% {
    transform: scale(1.6);
    opacity: 0;
  }
}



