@prefixTextarea: rc-textarea;

.@{prefixTextarea} {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: @textarea-content-padding;
    resize: vertical;
    &-wrap {
        font-size: @font-size-base;
        display: inline-block;
        width:208px;
        height:56px;
    }
    &:focus {
        outline: none;
        border: solid 1px #a3d7ef;   
        box-shadow: 0 0 4px #e4f1f7;
    }
    &:disabled{
        background-color: #ececec;
        color: #bdbdbd;
        border: none;
        cursor: not-allowed;
    }
     &-error {
        border: solid 1px #fe9898;
        box-shadow: 0 0 4px #fdcbcb;
    }
    &-error-text {
        color: #fe9898;
        font-size: 10px;
        margin: 5px 0 0;
        display: block;
    }
    &-error-hide {
        display: none;
    }
}


