@prefixSearchBox: rc-searchbox;


.@{prefixSearchBox} {
    &-wrap {
        display: inline-block;
        font-size: 12px;
        border: @searchbox-border;
        width: 205px;
        height: 32px;
        line-height: 30px;
        position: relative;
        box-sizing: border-box;
    }
    &-input-container {
        margin-right: 32px;
        height: 100%;
    }
    &-input-wrap {
        height: 29px;
    }
    &-input {
        border: none;
        text-align: center;
        text-indent: 0;
    }
    &-input:focus {
        border: none;
        outline: none;
        box-shadow: none;
        text-align: left;
        text-indent: 8px;
    }
    &-delete {
        cursor: pointer;
        display: inline-block;
        text-align: center;
        position: absolute;
        right: 33px;
        width: 30px;
        height: 100%;
        background-color: #fff;
        outline: none;
    }
    &-hide {
        display: none;
    }
    &-idelete {
        color: #d6d6d6;
        cursor: pointer;  
    }
    &-icon-wrap {
        width: 32px;
        text-align: center;
        display: inline-block;
        border-left: @searchbox-border;
        position: absolute;
        right: 0;
        top: 3px;
        bottom: 3px;
        outline: none;
    }
    &-isearch {
        font-size: 18px;
        color: #d6d6d6;
        cursor: pointer;
    }
}


