@import url('./iconfont.less');

@font-family-iconfont: propheticon;
@src-iconfont-prefix: './iconfont/iconfont';

@font-face {
  font-family: @font-family-iconfont;
  src: url('@{src-iconfont-prefix}.eot'); /* IE9*/
  src: 
  url('@{src-iconfont-prefix}.woff') format('woff'), /* chrome, firefox */
  url('@{src-iconfont-prefix}.ttf') format('truetype'); /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
}

.iconfont-mixin() {
  display: inline-block;
  font-style: normal;
  vertical-align: baseline;
  text-align: center;
  text-transform: none;
  text-rendering: auto;
  line-height: 1;

  &:before {
    display: block;
    font-family: @font-family-iconfont !important;
  }
}

.icon {
 .iconfont-mixin();
}



