@prefix-btn: rc-btn;

.@{prefix-btn}-sz-default {
  min-width: @btn-width-lg;
  min-height: @btn-height-lg;
}

.@{prefix-btn} {
  border-radius: @border-radius-sm;
  font-size: @font-size-base;
  cursor: pointer;
  border: none;
  outline: none;
  display: inline-block;
  overflow: hidden;
  -webkit-transition: all .3s;
  transition: all .3s;
  vertical-align: middle;

  &-primary {
    .rc-btn-sz-default();
    background: @btn-primary-bg;
    color: @btn-primary-color;
  }

  &-primary:hover {
    background: @btn-primary-hover-bg;
  }

  &-default {
    .rc-btn-sz-default();
    box-shadow: @box-shadow-base;
    background: @btn-default-bg;
    color: @btn-default-color;
    opacity: @btn-opacity;
  }

  &-default:hover{
    background: @btn-disable-bg;
  }

  &-box{
    .rc-btn-sz-default();
    background: @btn-box-bg;
    color: @btn-box-color;
    border: 1px solid @border-color-base;
  }

  &-box:hover{
    background: @primary-color;
    color: @btn-box-bg;
  }

  &-lg {
    min-width: @btn-width-lg;
    min-height: @btn-width-lg;
  }

  &-sm {
    min-width: @btn-width-sm;
    min-height: @btn-height-sm;
    box-shadow: @box-shadow-base;
  }

  &-icon {
    margin-right: 5px;
  }

  &:disabled {
    color: @btn-disable-color;
    background: @btn-disable-bg;
    border: @btn-disable-border;
    cursor: @cursor-disabled;
  }

  &&-loading {
    pointer-events: none;
    opacity: 0.75;
    .icon:before {
      -webkit-animation: loadingCircle 1.5s infinite linear;
      animation: loadingCircle 1.5s infinite linear;
      height: 12px;
      line-height: 12px;
      left: 10px;
      top: 50%;
      margin-top: -6px;
      opacity: 1;
      visibility: visible;
    }
  }
}

@keyframes loadingCircle {
  0% {
    transform-origin: 50% 50%;
    transform: rotate(0deg);
  }
  100% {
    transform-origin: 50% 50%;
    transform: rotate(360deg);
  }
}



