.rc-pager {
    height: 26px;
    line-height: 26px;
    font-size: 12px;
    .rc-pager-text {
        display: inline-block;
        vertical-align: top;
    }
    .rc-pager-jumper {
        margin-left: 10px;
        display: inline-block;
    }
    a {
        margin-left: 10px;
        color: @primary-color;
        &:hover {
            opacity: 0.8;
        }
    }
    .rc-select {
        vertical-align: top;
        margin-left: 5px;
    }
    .rc-input-wrap {
        width: 30px;
        height: 26px;
        margin-left: 5px;
        .rc-input {
            text-align: center;
            text-indent: 0;
        }
    }
    .rc-pager-right {
        float: right;
    }
}



