@prefixInput: rc-input;

.@{prefixInput} {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    text-indent: 5px;
    border-radius: @border-radius-md;
    border: solid 1px #d6d6d6;
    -webkit-transition: all 0.2s;
       -moz-transition: all 0.2s;
         -o-transition: all 0.2s;
            transition: all 0.2s; 
    &-wrap {
        width: 100%;
        height: 32px;
        color: #000;
        font-size: @font-size-base;
        border-radius: @border-radius-md;
        display: inline-block;
    }
    &:focus {
        outline: none;
        border: solid 1px #a3d7ef;   
        box-shadow: 0 0 4px #e4f1f7;
    }
    &:disabled{
        background-color: #ececec;
        color: #bdbdbd;
        border: none;
        cursor: not-allowed;
    }

    &-error {
        border: solid 1px #fe9898;
        box-shadow: 0 0 4px #fdcbcb;
    }

    &-error-text {
        color: #fe9898;
        font-size: 10px;
        margin: 5px 0 0;
        display: block;
    }
    &-error-hide {
        display: none;
    }
}



