
@prefixMenu: rc-menu;


.@{prefixMenu} {
    & {
        outline: none;
        margin-bottom: 0;
        padding-left: 0;
        list-style: none;
        z-index: 99999;
        border: 1px solid #d9d9d9;
        box-shadow: 0 0 1px rgba(0,0,0,.14);
        border-radius: 3px;
        color: rgba(0,0,0,.54);
    }
    &-wrap {
        width: 207px;
        font-size: @font-size-base;
    }
    &-item {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    &-item, &-submenu {
        height: 34px;
        line-height: 34px;
    }
    &-item:hover, &-submenu:hover {
        background-color: rgba(0, 0, 0, .09);
    }
    &-icon-wrap {
        width: 14px;
        height: 14px;
        display: inline-block;
        margin-right: 10px;
    }
    &-subwrap {
        display: inline-block;
        width: 100%;
        .ibefore {
            display: inline-block;
            margin-right: 10px;
        }
        .iafter{
            display: inline-block;
            float: right;
        }
    }
    &-disabled{
        color: #bdbdbd;
        cursor: not-allowed;
    }
}

// 以下为rc-menu自带css

.rc-menu-hidden {
    display: none;
}
.rc-menu-collapse {
    overflow: hidden;
}
.rc-menu-collapse-active {
    -webkit-transition: height 0.3s ease-out;
    transition: height 0.3s ease-out;
}
.rc-menu-item-group-list {
    margin: 0;
    padding: 0;
}
.rc-menu-item-group-title {
    color: #999;
    line-height: 1.5;
    padding: 8px 10px;
    border-bottom: 1px solid #dedede;
}
.rc-menu > li.rc-menu-submenu {
    padding: 0;
}
.rc-menu-submenu-horizontal > .rc-menu {
    top: 100%;
    left: 0;
    position: absolute;
    min-width: 160px;
    margin-top: 4px;
}
.rc-menu-submenu-vertical > .rc-menu {
    top: -5px;
    left: 100%;
    position: absolute;
    min-width: 160px;
}
.rc-menu-item,
.rc-menu-submenu-title {
    margin: 0;
    position: relative;
    display: block;
    padding: 7px 7px 7px 16px;
    white-space: nowrap;
}
.rc-menu-item.rc-menu-item-disabled,
.rc-menu-submenu-title.rc-menu-item-disabled,
.rc-menu-item.rc-menu-submenu-disabled,
.rc-menu-submenu-title.rc-menu-submenu-disabled {
    color: #bdbdbd;
    cursor: not-allowed;
}
.rc-menu > .rc-menu-item-divider {
    height: 1px;
    margin: 1px 0;
    overflow: hidden;
    padding: 0;
    line-height: 0;
    background-color: #e5e5e5;
}
.rc-menu-submenu {
    position: relative;
}
.rc-menu-submenu > .rc-menu {
    background-color: #fff;
}
.rc-menu .rc-menu-submenu-title .anticon,
.rc-menu .rc-menu-item .anticon {
    width: 14px;
    height: 14px;
    margin-right: 8px;
    top: -1px;
}
.rc-menu-horizontal {
    background-color: #F3F5F7;
    border: none;
    border-bottom: 1px solid transparent;
    border-bottom: 1px solid #d9d9d9;
    box-shadow: none;
}
.rc-menu-horizontal > .rc-menu-item,
.rc-menu-horizontal > .rc-menu-submenu > .rc-menu-submenu-title {
    padding: 15px 20px;
}
.rc-menu-horizontal > .rc-menu-submenu,
.rc-menu-horizontal > .rc-menu-item {
    float: left;
    border-bottom: 2px solid transparent;
}
.rc-menu-horizontal > .rc-menu-submenu-active,
.rc-menu-horizontal > .rc-menu-item-active {
    border-bottom: 2px solid #2db7f5;
    background-color: #F3F5F7;
    color: #2baee9;
}
.rc-menu-horizontal:before {
    content: "\E661";
    display: block;
    height: 0;
    clear: both;
}
.rc-menu-vertical > .rc-menu-item,
.rc-menu-inline > .rc-menu-item,
.rc-menu-vertical > .rc-menu-submenu > .rc-menu-submenu-title,
.rc-menu-inline > .rc-menu-submenu > .rc-menu-submenu-title {
    padding: 0 24px 0 18px;
}
.rc-menu-vertical.rc-menu-sub {
  padding: 0;
  border: none;
}
.rc-menu-sub.rc-menu-inline {
  padding: 0;
  border: none;
  border-radius: 0;
  box-shadow: none;
}
.rc-menu-sub.rc-menu-inline > .rc-menu-item,
.rc-menu-sub.rc-menu-inline > .rc-menu-submenu > .rc-menu-submenu-title {
  padding-top: 8px;
  padding-bottom: 8px;
  padding-right: 0;
}
.rc-menu-open-slide-up-enter,
